<template>
  <the-header></the-header>
  <main>
    <router-view v-slot="slotProps">
      <transition name="route" mode="out-in">
        <component :is="slotProps.Component"></component>
      </transition>
    </router-view>
  </main>
</template>

<script>
import TheHeader from './components/layout/TheHeader';
import { mapActions, mapGetters } from 'vuex';

export default {
  components: {
    TheHeader
  },
  computed: {
    ...mapGetters(['didAutoSignOut'])
  },
  methods: {
    ...mapActions(['loadCoaches', 'autoLogin'])
  },
  created() {
    this.loadCoaches();
    this.autoLogin();
  },
  watch: {
    didAutoSignOut(newVal, oldVal) {
      if (!oldVal && oldVal !== newVal) {
        this.$router.replace('/coaches');
      }
    }
  }
};
</script>

<style lang="scss">
@import 'src/variables.scss';

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: $color-black;
  background-color: rgb(240, 240, 240);
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

section {
  width: 100%;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: $color-primary;
  transition: $transition-all;
  cursor: pointer;

  &:hover {
    color: $color-primary-dark;
  }
}

h1 {
  font-size: 2.8rem;
  margin-bottom: 1.4rem;
  font-family: monospace;
}

h2 {
  font-size: 2rem;
  margin-bottom: 1.2rem;
}

h3 {
  font-size: 1.8rem;
  margin-bottom: 1rem;
}

h4 {
  font-size: 1.4rem;
  margin-bottom: 0.8rem;
}

.error {
  text-align: center;
  font-size: 3rem;
  font-weight: bold;
  margin-top: 10rem;
}

.inline-error {
  font-size: 1.4rem;
  color: $color-error;
  text-align: center;
  margin: 2rem 0;
}

.v-enter-active {
  animation: component 0.3s ease-in forwards;
}
.v-leave-active {
  animation: component 0.3s ease-in reverse;
}

.route-enter-active {
  animation: errow 0.3s ease-in forwards;
}
.route-leave-active {
  animation: errow 0.3s ease-in reverse;
}

.error-enter-active {
  animation: errow 0.3s ease-in forwards;
}
.error-leave-active {
  animation: errow 0.3s ease-out reverse;
}
</style>
